import React from 'react';
import {NavLink} from "react-router-dom";

function Menu(props) {
    return (
        <ul className={'menu'}>
            <li>
                <NavLink to={"/"}>Home</NavLink>
            </li>
            <li>
                <NavLink to={"/member/form"}></NavLink>
            </li>
            <li>
                <NavLink to={"/member/list"}></NavLink>
            </li>
            <li>
                <NavLink to={"/board/list"}></NavLink>
            </li>
            <li>
                <NavLink to={"/login"}></NavLink>
            </li>
        </ul>
    );
}

export default Menu;